@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;

}


.primary_button{
    font-size: 13px;
    padding: 3px 15px;   
    background-color: rgb(0,128,128);
    border: solid 4px teal;
    color: white;
    opacity: .7;
    border-radius: 25px;
    cursor: pointer;
}
.hidden{
    display: none;
}
.primary_button:hover{
    opacity: .9;
}
.secondary_button{
    font-size: 13px;
    padding: 3px 15px;   
    background-color: gray;
    border: solid 4px gray;
    color: white;
    opacity: .7;
    border-radius: 25px;
    cursor: pointer;
    margin-left: 10px ;

}
.secondary_button:hover{
    opacity: .9;
}

@media only screen and (max-width: 599px) {
    .primary_button{
        font-size: 10px;
        opacity: 1;
        padding: 5px;   
        
    }
    .scrollspy{
        display: flex;
        flex-direction: column;
    }
} 

html {
    scroll-behavior: smooth;
}  
hr {
    width: 100%;
    opacity: .3;
}
.App{
    font-family: 'Poppins', sans-serif;
}
a{
    color: #fff;
    text-decoration: none;
    letter-spacing: 0.15em;
    font-size: .9em;
}
.resume_button{
    background: transparent;
    color: rgb(0,128,128);
    border: none;
    -webkit-transform: scale(2);
            transform: scale(2);
    opacity: .4;
    cursor: pointer;
    position: -webkit-sticky;
    position: sticky;
    top: 30px;
}
.resume_button:hover{
    color: gray;
    opacity: 1
}
input:focus, textarea:focus, select:focus{
    outline: none;
}
.flex{
    display: flex;
    align-items: flex-start;
}

